import { useEffect, useRef } from "react";
import { FaceLandmarker, FilesetResolver } from "@mediapipe/tasks-vision";

// Linear interpolation function
const lerp = (start, end, t) => {
  return start * (1 - t) + end * t;
};

export const useWebcamTracking = ({
  videoRef,
  debugCanvasRef,
  showDebugView,
  setMouthOpen,
  setBlendshapeMouthOpen,
  setRotation,
  setPosition,
  setEyeRotation,
  setBlendshapeNames,
  smoothingFactor = 0.3,
  positionSmoothingFactor = 0.3, // New smoothing factor for position
}) => {
  const faceLandmarkerRef = useRef(null);
  const cameraRef = useRef(null);
  const previousPositionRef = useRef({ x: 0, y: 0, z: 0 }); // Initialize previous position
  const previousRotationRef = useRef({ yaw: 0, pitch: 0, roll: 0, eyeRotationX: 0, eyeRotationY: 0 }); // Initialize previous rotation

  useEffect(() => {
    const initializeFaceLandmarker = async () => {
      const filesetResolver = await FilesetResolver.forVisionTasks(
        "https://cdn.jsdelivr.net/npm/@mediapipe/tasks-vision/wasm"
      );

      const faceLandmarker = await FaceLandmarker.createFromOptions(
        filesetResolver,
        {
          baseOptions: {
            modelAssetPath:
              "https://storage.googleapis.com/mediapipe-models/face_landmarker/face_landmarker/float16/latest/face_landmarker.task",
          },
          runningMode: "VIDEO",
          outputFaceBlendshapes: true,
          outputFacialTransformationMatrixes: true,
          numFaces: 1,
        }
      );

      faceLandmarkerRef.current = faceLandmarker;
    };

    initializeFaceLandmarker();

    return () => {
      if (faceLandmarkerRef.current) {
        faceLandmarkerRef.current.close();
        faceLandmarkerRef.current = null;
      }
    };
  }, []);

  const startCamera = async () => {
    try {
      const stream = await navigator.mediaDevices.getUserMedia({
        video: true,
        audio: false,
      });

      if (videoRef.current) {
        videoRef.current.srcObject = stream;
      }

      if (faceLandmarkerRef.current && videoRef.current) {
        const processFrame = async () => {
          if (!videoRef.current.paused && !videoRef.current.ended) {
            faceLandmarkerRef.current.setOptions({ runningMode: "VIDEO" });

            const results = await faceLandmarkerRef.current.detectForVideo(
              videoRef.current,
              performance.now()
            );

            if (results.faceBlendshapes.length > 0) {
              // Log all available blendshape names on first detection
              if (!faceLandmarkerRef.current.hasLoggedBlendshapes) {
                const names = results.faceBlendshapes[0].categories.map(category => category.categoryName);
                setBlendshapeNames(names);
                faceLandmarkerRef.current.hasLoggedBlendshapes = true;
              }

              const mouthOpenBlendshape = results.faceBlendshapes[0].categories.find(
                (category) => category.categoryName === "jawOpen"
              );
              if (mouthOpenBlendshape) {
                setBlendshapeMouthOpen(mouthOpenBlendshape.score);
                setMouthOpen(mouthOpenBlendshape.score);
              }

              // Calculate eye rotation from blendshapes
              const eyeBlendshapes = results.faceBlendshapes[0].categories;
              const eyeLookUpRight = eyeBlendshapes.find(cat => cat.categoryName === "eyeLookUpRight")?.score || 0;
              const eyeLookDownRight = eyeBlendshapes.find(cat => cat.categoryName === "eyeLookDownRight")?.score || 0;
              const eyeLookInRight = eyeBlendshapes.find(cat => cat.categoryName === "eyeLookInRight")?.score || 0;
              const eyeLookOutRight = eyeBlendshapes.find(cat => cat.categoryName === "eyeLookOutRight")?.score || 0;

              // Calculate eye rotation angles
              const eyeRotationX = (eyeLookUpRight - eyeLookDownRight) * Math.PI / 2; // Up/Down rotation
              const eyeRotationY = (eyeLookOutRight - eyeLookInRight) * Math.PI / 2; // Left/Right rotation

              // Smooth eye rotation values
              const smoothedEyeRotationX = lerp(previousRotationRef.current.eyeRotationX || 0, eyeRotationX, 0.5);
              const smoothedEyeRotationY = lerp(previousRotationRef.current.eyeRotationY || 0, eyeRotationY, 0.5);

              // Update previous rotation for next frame
              previousRotationRef.current = {
                ...previousRotationRef.current,
                eyeRotationX: smoothedEyeRotationX,
                eyeRotationY: smoothedEyeRotationY
              };

              // Set the rotation including eye rotation
              setEyeRotation(prev => ({
                ...prev,
                eyeRotationX: smoothedEyeRotationX,
                eyeRotationY: smoothedEyeRotationY,
                eyeLookUpRight,
                eyeLookDownRight,
                eyeLookInRight,
                eyeLookOutRight
              }));
            }

            if (results.facialTransformationMatrixes.length > 0) {
              const matrix = results.facialTransformationMatrixes[0].data;
              
              // Extract rotation values (yaw, pitch, roll) from the matrix
              const yaw = Math.asin(-matrix[2]);
              const pitch = Math.atan2(matrix[6], matrix[10]);
              const roll = Math.atan2(matrix[1], matrix[0]);
              
              // Smooth rotation values
              const smoothedYaw = lerp(previousRotationRef.current.yaw, yaw, 0.25);
              const smoothedPitch = lerp(previousRotationRef.current.pitch, pitch, 0.25);
              const smoothedRoll = lerp(previousRotationRef.current.roll, roll, 0.25);
              
              // Update previous rotation for next frame
              previousRotationRef.current = {
                yaw: smoothedYaw,
                pitch: smoothedPitch,
                roll: smoothedRoll
              };

              // Set the rotation
              setRotation({
                yaw: smoothedYaw,
                pitch: smoothedPitch,
                roll: smoothedRoll
              });
            }

            if (results.faceLandmarks.length > 0) {
              const faceLandmarks = results.faceLandmarks[0];

              // Extracting the key landmarks: nose, left eye, right eye
              const nose = faceLandmarks[1]; // Nose tip (landmark 1)
              const leftEye = faceLandmarks[234];  // Left eye region (landmark 234)
              const rightEye = faceLandmarks[454]; // Right eye region (landmark 454)

              // Positioning calculation from landmarks
              const newPosition = {
                // Map the normalized coordinates to your 3D space
                // x, y, z represent the head's position in the 3D space

                // X and Y based on the nose position
                x: (nose.x - 0.5) * 4, // Scale and center the X-coordinate
                y: -(nose.y - 0.5) * 4, // Scale and invert the Y-coordinate (because y increases down in canvas space)
                
                // Z based on the relative position of the nose to the eyes
                z: (nose.z * -50) - 5, // Invert and scale the Z coordinate
              };

              // Apply smoothing to the position
              const smoothedPosition = {
                x: lerp(previousPositionRef.current.x, newPosition.x, 0.5),
                y: lerp(previousPositionRef.current.y, newPosition.y, 0.5),
                z: lerp(previousPositionRef.current.z, newPosition.z, 0.5),
              };

              // Update previous position reference for next frame
              previousPositionRef.current = smoothedPosition;

              // Update position
              setPosition(smoothedPosition);
            }

            if (showDebugView && debugCanvasRef.current) {
              const ctx = debugCanvasRef.current.getContext("2d");
              ctx.clearRect(
                0,
                0,
                debugCanvasRef.current.width,
                debugCanvasRef.current.height
              );
              ctx.drawImage(
                videoRef.current,
                0,
                0,
                debugCanvasRef.current.width,
                debugCanvasRef.current.height
              );

              // Optionally draw the landmarks on the debug canvas
              if (results.faceLandmarks.length > 0) {
                const faceLandmarks = results.faceLandmarks[0];
                ctx.fillStyle = "red";
                faceLandmarks.forEach((landmark) => {
                  ctx.beginPath();
                  ctx.arc(
                    landmark.x * debugCanvasRef.current.width,
                    landmark.y * debugCanvasRef.current.height,
                    1,
                    0,
                    2 * Math.PI
                  );
                  ctx.fill();
                });
              }
            }
          }

          requestAnimationFrame(processFrame);
        };

        videoRef.current.onloadedmetadata = () => {
          videoRef.current.play();
          processFrame();
        };
      }
    } catch (error) {
      console.error("Error starting camera:", error);
      throw error;
    }
  };

  const stopCamera = () => {
    if (cameraRef.current) {
      cameraRef.current.stop();
      cameraRef.current = null;
    }
    if (videoRef.current?.srcObject) {
      videoRef.current.srcObject.getTracks().forEach((track) => track.stop());
      videoRef.current.srcObject = null;
    }
    if (debugCanvasRef.current) {
      const ctx = debugCanvasRef.current.getContext("2d");
      if (ctx) ctx.clearRect(0, 0, debugCanvasRef.current.width, debugCanvasRef.current.height);
    }
  };

  return {
    startCamera,
    stopCamera,
  };
};
