import "./styles.css";
import React, { useState, useRef, useEffect } from "react";
import { Canvas, useFrame } from "@react-three/fiber";
import { useRive } from "@rive-app/react-canvas";
import * as THREE from "three";
import { useWebcamTracking } from "./useWebcamTracking";

const toDegrees = (radians) => (radians * 180) / Math.PI;
const toRadians = (degrees) => (degrees * Math.PI) / 180;

function Face({ mouthOpen, rotation, eyeRotation, debugRotation, useDebugRotation, position }) {
  const faceRef = useRef();
  const mouthRef = useRef();
  const leftEyeRef = useRef();
  const rightEyeRef = useRef();

  useFrame(() => {
    if (faceRef.current) {
      if (useDebugRotation) {
        faceRef.current.rotation.set(debugRotation.pitch, debugRotation.yaw, debugRotation.roll);
      } else {
        faceRef.current.rotation.set(rotation.pitch, rotation.yaw, rotation.roll);
      }
      faceRef.current.position.set(position.x, position.y, position.z);
    }
    if (mouthRef.current) {
      let mouthScale = 1 + mouthOpen * 10;
      mouthRef.current.scale.set(mouthScale * 1.5, mouthScale, 1);
      mouthRef.current.position.y = 0 + mouthOpen * 0.15;
    }
    if (leftEyeRef.current) {
      leftEyeRef.current.rotation.set(eyeRotation.eyeRotationX, -eyeRotation.eyeRotationY, 0);
    }
    if (rightEyeRef.current) {
      rightEyeRef.current.rotation.set(eyeRotation.eyeRotationX, -eyeRotation.eyeRotationY, 0);
    }
  });

  return (
    <group ref={faceRef}>
      <group position={[0, 0, 0]}>
        <mesh>
          <sphereGeometry args={[2, 32, 32]} />
          <meshStandardMaterial color="lightblue" />
        </mesh>
        {/* Left eye */}
        <group ref={leftEyeRef} position={[-0.78, 0.9, 1.4]}>
          <mesh>
            <sphereGeometry args={[0.5, 16, 16]} />
            <meshStandardMaterial color="white" />
          </mesh>
          <mesh position={[0, 0, 0.4]}>
            <sphereGeometry args={[0.15, 16, 16]} />
            <meshStandardMaterial color="black" />
          </mesh>
        </group>
        {/* Right eye */}
        <group ref={rightEyeRef} position={[0.75, 1, 1.4]}>
          <mesh>
            <sphereGeometry args={[0.5, 16, 16]} />
            <meshStandardMaterial color="white" />
          </mesh>
          <mesh position={[0, 0, 0.4]}>
            <sphereGeometry args={[0.15, 16, 16]} />
            <meshStandardMaterial color="black" />
          </mesh>
        </group>
        <mesh ref={mouthRef} position={[0, 0, 2]}>
          <sphereGeometry args={[0.05, 32, 32]} />
          <meshStandardMaterial color="black" />
        </mesh>
      </group>
    </group>
  );
}


function RiveMouthControl({ mouthOpen }) {
  const { RiveComponent, rive } = useRive({
    src: "/monster_mouth.riv",
    stateMachines: "State Machine 1",
    autoplay: true,
  });

  useEffect(() => {
    if (!rive) return;
    const inputs = rive.stateMachineInputs("State Machine 1");
    if (!inputs) return;
    const mouthYInput = inputs.find((input) => input.name === "mouthY");
    if (mouthYInput) {
      mouthYInput.value = mouthOpen * 100;
    }
  }, [mouthOpen, rive]);

  return <RiveComponent className="rive-animation" />;
}

function App() {
  const [isRecording, setIsRecording] = useState(false);
  const [showDebugView, setShowDebugView] = useState(true);
  const [show3D, setShow3D] = useState(true);
  const [mouthOpen, setMouthOpen] = useState(0);
  const [blendshapeMouthOpen, setBlendshapeMouthOpen] = useState(0);
  const [rotation, setRotation] = useState({ yaw: 0, pitch: 0, roll: 0 });
  const [eyeRotation, setEyeRotation] = useState({ eyeRotationX: 0, eyeRotationY: 0, eyeLookUpRight: 0, eyeLookDownRight: 0, eyeLookInRight: 0, eyeLookOutRight: 0 });
  const [position, setPosition] = useState({ x: 0, y: 0, z: 0 });
  const [blendshapeNames, setBlendshapeNames] = useState([]);
  const videoRef = useRef(null);
  const canvasRef = useRef(null);
  const debugCanvasRef = useRef(null);
  const mediaRecorderRef = useRef(null);
  const chunksRef = useRef([]);
  const [debugRotation, setDebugRotation] = useState({ pitch: 0, yaw: 0, roll: 0 });
  const [useDebugRotation, setUseDebugRotation] = useState(false);
  const [rotationOffsets, setRotationOffsets] = useState({ 
    pitch: toRadians(-90),
    yaw: toRadians(90),
    roll: 0 
  });
  const [showPreview, setShowPreview] = useState(false);
  const [videoURL, setVideoURL] = useState(null);
  const riveCanvasRef = useRef(null);
  const previousRotationRef = useRef({ pitch: 0, yaw: 0, roll: 0 });
  const previousPositionRef = useRef({ x: 0, y: 0, z: 0 });

  const { startCamera, stopCamera } = useWebcamTracking({
    videoRef,
    debugCanvasRef,
    showDebugView,
    setMouthOpen,
    setBlendshapeMouthOpen,
    setRotation,
    setEyeRotation,
    setPosition,
    setBlendshapeNames,
    smoothingFactor: 0.3,
    positionSmoothingFactor: 0.3,
  });

  const stopRecording = () => {
    if (mediaRecorderRef.current) {
      mediaRecorderRef.current.stop();
      mediaRecorderRef.current = null;
    }
    stopCamera();
    setIsRecording(false);
    setShowPreview(true);
  };

  const startRecording = async () => {
    try {
      await startCamera();

      // Get the canvas streams
      const canvasStream = show3D
        ? riveCanvasRef.current.captureStream(30)
        : document.querySelector("canvas").captureStream(30);

      const combinedStream = new MediaStream([
        ...canvasStream.getTracks(),
        ...videoRef.current.srcObject.getTracks()
      ]);

      const mediaRecorder = new MediaRecorder(combinedStream, {
        mimeType: "video/webm;codecs=vp9"
      });
      mediaRecorderRef.current = mediaRecorder;
      const videoChunks = [];
      mediaRecorder.ondataavailable = (event) => videoChunks.push(event.data);
      mediaRecorder.onstop = () => {
        const videoBlob = new Blob(videoChunks, { type: "video/webm" });
        setVideoURL(URL.createObjectURL(videoBlob));
        setShowPreview(true);
      };
      mediaRecorder.start();
      setIsRecording(true);
    } catch (error) {
      alert("Could not start recording. Please check permissions.");
    }
  };

  const handleDebugRotationChange = (axis, value) => {
    setDebugRotation(prev => ({
      ...prev,
      [axis]: toRadians(parseFloat(value))
    }));
  };

  const handleOffsetChange = (axis, value) => {
    setRotationOffsets(prev => ({
      ...prev,
      [axis]: toRadians(parseFloat(value))
    }));
  };

  return (
    <div className="app-container">
      <h1>Puppeteer</h1>
      {!showPreview ? (
        <div className="screen-one">
          <div className="canvas-container">
            {show3D ? (
              <Canvas
                ref={riveCanvasRef}
                style={{ backgroundColor: "lightyellow" }}
              >
                <ambientLight intensity={0.5} />
                <pointLight position={[5, 5, 5]} />
                <Face 
                  mouthOpen={mouthOpen} 
                  rotation={rotation} 
                  eyeRotation={eyeRotation}
                  debugRotation={debugRotation}
                  useDebugRotation={useDebugRotation}
                  position={position}
                />
              </Canvas>
            ) : (
              <RiveMouthControl mouthOpen={mouthOpen} />
            )}
          </div>
          <div className="controls-container">
            <label className="toggle-label">
              3D
              <input
                type="checkbox"
                checked={show3D}
                onChange={() => setShow3D((prev) => !prev)}
              />
              <span className="toggle"></span>
            </label>
            <label className="toggle-label">
              Debug View
              <input
                type="checkbox"
                checked={showDebugView}
                onChange={() => setShowDebugView((prev) => !prev)}
              />
              <span className="toggle"></span>
            </label>
            <label className="toggle-label">
              Manual Rotation
              <input
                type="checkbox"
                checked={useDebugRotation}
                onChange={() => setUseDebugRotation((prev) => !prev)}
              />
              <span className="toggle"></span>
            </label>
            <div className="rotation-controls">
              {useDebugRotation && (
                <>
                  <h3>Manual Rotation</h3>
                  <div className="rotation-slider">
                    <label>Pitch (X)</label>
                    <input
                      type="range"
                      min="-180"
                      max="180"
                      step="1"
                      value={toDegrees(debugRotation.pitch)}
                      onChange={(e) => handleDebugRotationChange('pitch', e.target.value)}
                    />
                    <span>{toDegrees(debugRotation.pitch).toFixed(1)}</span>
                  </div>
                  <div className="rotation-slider">
                    <label>Yaw (Y)</label>
                    <input
                      type="range"
                      min="-180"
                      max="180"
                      step="1"
                      value={toDegrees(debugRotation.yaw)}
                      onChange={(e) => handleDebugRotationChange('yaw', e.target.value)}
                    />
                    <span>{toDegrees(debugRotation.yaw).toFixed(1)}</span>
                  </div>
                  <div className="rotation-slider">
                    <label>Roll (Z)</label>
                    <input
                      type="range"
                      min="-180"
                      max="180"
                      step="1"
                      value={toDegrees(debugRotation.roll)}
                      onChange={(e) => handleDebugRotationChange('roll', e.target.value)}
                    />
                    <span>{toDegrees(debugRotation.roll).toFixed(1)}</span>
                  </div>
                </>
              )}
            </div>
            <div className="debug-text">
              <p>Processed Mouth Open: {mouthOpen.toFixed(3)}</p>
              <p>Original Blendshape: {blendshapeMouthOpen.toFixed(3)}</p>
              <p>Eye Look Up Right: {eyeRotation.eyeLookUpRight?.toFixed(3) || 0}</p>
              <p>Eye Look Down Right: {eyeRotation.eyeLookDownRight?.toFixed(3) || 0}</p>
              <p>Eye Look In Right: {eyeRotation.eyeLookInRight?.toFixed(3) || 0}</p>
              <p>Eye Look Out Right: {eyeRotation.eyeLookOutRight?.toFixed(3) || 0}</p>
              <p>Eye Rotation X: {eyeRotation.eyeRotationX?.toFixed(3) || 0}</p>
              <p>Eye Rotation Y: {eyeRotation.eyeRotationY?.toFixed(3) || 0}</p>
              <div className="blendshape-names">
                <h3>Available Blendshapes:</h3>
                {blendshapeNames.map((name, index) => (
                  <p key={index}>{name}</p>
                ))}
              </div>
            </div>
            {isRecording ? (
              <button onClick={stopRecording} className="control-button">
                Finish Recording
              </button>
            ) : (
              <button onClick={startRecording} className="control-button">
                Start Recording
              </button>
            )}
          </div>
          <video
            ref={videoRef}
            style={{ display: 'none' }}
            width="640"
            height="480"
            autoPlay
            playsInline
          />
          {showDebugView && (
            <div className="debug-container">
              <video
                ref={videoRef}
                style={{ display: 'block' }}
                width="160"
                height="120"
                autoPlay
                playsInline
              />
              <canvas
                ref={debugCanvasRef}
                width="160"
                height="120"
                style={{ position: 'absolute', top: 0, left: 0 }}
              />
            </div>
          )}
        </div>
      ) : (
        <div className="screen-two">
          <div className="video-preview-container">
            <video controls src={videoURL} className="video-player"></video>
          </div>
          <div className="buttons-container">
            <button
              onClick={() => setShowPreview(false)}
              className="control-button left-button"
            >
              Redo Recording
            </button>
            <a href={videoURL} download="recording.webm">
              <button className="download-button right-button">
                Download Video
              </button>
            </a>
          </div>
        </div>
      )}
    </div>
  );
}

export default App;
